* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Golos Text", sans-serif;
  font-family: "Poppins", sans-serif;
  font-family: "Roboto", sans-serif;
}

#page-wrap {
  padding-bottom: 10px;
  padding-top: 10px;
}

.navbarBar {
  background-color: rgb(84, 84, 84);
  width: 100%;
  height: 75px;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1.5vw;
}

.domov {
  width: 100vw;
  height: 100vh;
  background-color: red;
  align-items: center;
}

.sluzby {
  width: 100%;
  height: 100vh;
  background-color: blue;
  align-items: center;
}

.galeria {
  width: 100%;
  height: 100vh;
  background-color: orange;
  padding-top: 20vh;
}
.galeria .fotka1 {
  background-image: url(/src/photos/cista1.png);
  background-size: cover;
}
.galeria .fotka2 {
  background-image: url(/src/photos/cista2.png);
  background-size: cover;
}
.galeria .fotka3 {
  background-image: url(/src/photos/cista3.png);
  background-size: cover;
}
.galeria .fotka4 {
  background-image: url(/src/photos/cista4.png);
  background-size: cover;
}

.onas {
  width: 100%;
  height: 100vh;
  background-color: green;
  align-items: center;
}

.kontakt {
  width: 100%;
  height: 100vh;
  background-color: purple;
  align-items: center;
}

.container .row {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.container .row .card1-2-1 {
  background-color: white;
  width: 50%;
  height: 200px;
  border-radius: 5px;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2vh;
}
.container .row .card1-2-1 .domov-2 {
  width: 90%;
}
.container .row .card1-2-2 {
  background-color: white;
  width: 30%;
  height: 200px;
  border-radius: 5px;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2vh;
}
.container .row .card1-2-2 .domov-2 {
  width: 70%;
}
.container .row .card2-3 {
  background-color: white;
  width: 25%;
  min-height: 200px;
  border-radius: 5px;
  margin: 20px;
  padding: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1vw;
}
.container .row .card2-3 .sluzby-2 {
  width: 80%;
}
.container .row .card2-3 .sluzby-2 .cas-umyvania {
  color: rgb(172, 52, 0);
}
.container .row .card3-8 {
  background-color: white;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container .row .card4-2 {
  background-color: white;
  width: 45%;
  height: 200px;
  border-radius: 5px;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container .row .card5-2 {
  background-color: white;
  width: 45%;
  height: 200px;
  border-radius: 5px;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pata {
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pata h4 {
  padding-left: 40px;
}

@media screen and (max-width: 1220px) {
  .card2-3 {
    flex: 100%;
    max-width: 70%;
    max-height: 200px;
  }
  .card2-3 .sluzby-2 {
    display: flex;
  }
  .card3-8 {
    flex: 33%;
    max-width: 33%;
    max-height: 240px;
  }
}
@media screen and (max-width: 992px) {
  .card1-2 {
    flex: 100%;
    max-width: 90%;
  }
  .card3-8 {
    flex: 45%;
    max-width: 45%;
    max-height: 60vw;
  }
}/*# sourceMappingURL=styles.css.map */